@import "../../styles/mixins/mixins.scss";
@import "../../styles/utils/variables.scss";

.itemCard {
  display: flex;
  box-sizing: border-box;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  flex-shrink: 0;
  background: $bg-color-product;
  width: 100%;
  height: 100%;
  transition: $animation-time;

  &:hover {
    box-shadow: inset 0 0 15px 5px $bg-color-controllers;
    transform: scale(101%);
  }

  &__link {
    text-decoration: none;
    margin: 0 auto;
  }

  &__errorImage {
    height: 96px;
    margin: 0 auto;
  }

  &__Title {
    display: flex;
    height: 42px;
    align-items: flex-end;
    align-self: stretch;

    &_Text {
      color: $primary-text-color;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 21px;
    }
  }

  &__Price {
    display: flex;
    align-items: center;
    gap: 8px;

    &_Current {
      color: $primary-text-color;
      font-size: 22px;
      font-style: normal;
      font-weight: 800;
      line-height: 140%;
    }

    &_Full {
      color: $secondary-text-color;
      font-size: 22px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      text-decoration-line: line-through;
    }
  }

  &__Specs {
    display: flex;
    padding: 8px 0px;
    flex-direction: column;
    position: relative;
    align-items: center;
    gap: 8px;
    align-self: stretch;

    &::before {
      content: "";
      height: 1px;
      width: 100%;
      background: $separator-color;
      position: absolute;
      top: -4px;
    }

    &_Container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      align-self: stretch;
    }

    &_Category {
      color: $secondary-text-color;
      font-size: 12px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
    }

    &_Value {
      color: $primary-text-color;
      text-align: right;
      font-size: 12px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
    }
  }

  &__ButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    align-self: stretch;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

.loaderContainer {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 196px;
}

.load {
  width: 100px;
  height: 100px;
  margin: auto;
  border: solid 10px $accent-color;
  border-radius: 50%;
  border-right-color: transparent;
  border-bottom-color: transparent;
  -webkit-transition: all 0.5s ease-in;
  -webkit-animation-name: rotate;
  -webkit-animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;

  transition: all 0.5s ease-in;
  animation-name: rotate;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.errorImageWrapper {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 196px;
}
